import { combineReducers, createStore } from "redux";
import { AuthReducer, AuthState } from "./AuthRedux";
import { couponReducer } from "./CouponRedux";
import { cartReducer } from "./CartRedux";
import { AdminReducer } from "./AdminRedux";




//multiplate reducers
//we will put here all our reducers :)
const reducers = combineReducers({adminState:AdminReducer, authState:AuthReducer, couponState:couponReducer, cartState:cartReducer}); 
//for getting data from store in multi reducers type
const store = createStore(reducers);
//store.getState().authState.jwt;


export default store;











        
