import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {useForm,SubmitHandler} from "react-hook-form";
import store from '../../redux/Store';
import FaceIcon from '@mui/icons-material/Face';
import axios from 'axios';
import { useState } from 'react';
import { CssBaseline, Grid } from '@mui/material';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import { forceLogoutAction, setOpenAction } from '../../redux/AuthRedux';
import { useNavigate } from 'react-router';
import globals from '../../../Utils/Globals';


interface formable{
  name:string;
  firstName:string;
  lastName:string;
  email:string;
  password:string;
  passwordAgain:string;
}

function Registr(): JSX.Element {

  const [equal,setEqule] = useState("");
  const [serverError, setServerError]=useState("");
  const {register,handleSubmit,formState:{errors}} = useForm<formable>();
  const theme = createTheme();
  const nav = useNavigate();

    
  
  console.log(window.location.href);

  const onSubmit:SubmitHandler<formable> = (data) => {
    if (data.password===data.passwordAgain){
        setEqule("");
        axios.post(globals.general.register,data).then(res=>{console.log(res);
                                        setServerError("You have successfully registered!");
                                        store.dispatch(forceLogoutAction());
                                        nav("/");})
                            .catch(err=>{console.log(err);
                              setServerError(err.response.data.description);});
      }else {setEqule("The passwords are not the same");}
    }
  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: '100vh' }}>
        <CssBaseline />
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: 'url(https://source.unsplash.com/random)',
            backgroundRepeat: 'no-repeat',
            backgroundColor: (t) =>
              t.palette.mode === 'light' ? t.palette.grey[50] : t.palette.grey[900],
            backgroundSize: 'cover',
            backgroundPosition: 'center',
          }}
        />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
          <Avatar>
            <FaceIcon/>
          </Avatar>
          <Typography component="h1" variant="h5">{"New customer registration"}</Typography>
            <Box component="form"  onSubmit={handleSubmit(onSubmit)} sx={{ mt: 1 }}>
              <TextField
                required
                style={{width: 140, height:50,margin:10}}
                id="firstName"
                label="First Name"
                variant="outlined" {...register("firstName",{maxLength:15, required:true})}
              />
              <TextField
                margin="normal"
                style={{width:140,height:50,margin:10}}
                label="Last name"
                id="lastName"
                defaultValue={store.getState().authState.lastName}
                variant="outlined" {...register("lastName",{maxLength:15, required:true})}
              />
              <br/>
              <TextField
                margin="normal"
                required
                style={{width: 300, height:50,margin:10}}
                label="Email"
                id="email"
                defaultValue={store.getState().authState.email}
                variant="outlined" {...register("email",{required:true})}
              />
              <br/>
              <TextField
                margin="normal"
                style={{width: 300, height:50,margin:10}}
                type="password"
                label="Password"
                id="Password"
                variant="outlined" {...register("password",{required:true})}
              />
              <br/>
              <TextField
                margin="normal"
                required
                type="password"
                style={{width: 300, height:50,margin:10}}
                label="New Password again"
                id="newPasswordAgain"
                variant="outlined" {...register("passwordAgain",{required:true})}
              />
              <br/>
              <div style={{color:"red"}}>
                {equal}
                {(errors.firstName && "name max length 15") || (errors.lastName && "last name max length 15")}
                {serverError}
              </div>
              <Button variant="contained" type="submit" style={{width: 300, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{"הרשמה"}</Button>
           
                
            </Box>
          </Box>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
}

export default Registr;

