import { useState } from "react";
import jwtAxios from "../../../Utils/Interceptor";

import UpdateUserCoupons from "../../coupons/UpdateUserCoupons/UpdateUserCoupons";
import UpdateStoreCoupon from "../../coupons/UpdateStoreCoupon/UpdateStoreCoupon";
import { Coupon } from "../../models/Coupon";
import "../PoppingForm.css";
import store from "../../redux/Store";
import GetAllCompanies from "../../adminActions/GetAllCompanies";
import { Alert, Button } from "@mui/material";
import GetAllCoustomer from "../../adminActions/GetAllCustomers";
import globals from "../../../Utils/Globals";

interface myProps {
    delet:any;
    func:Function;
}




function ConfirmDelete(props:myProps): JSX.Element {

    const handleDelete=()=> {
        let url="";
        if (props.delet.startDate) {url=globals.admin.deleteCoupon+props.delet.id;}
        if (!props.delet.startDate){url=globals.admin.deleteCompany+props.delet.id;};
        if (props.delet.lastName){url=globals.admin.deleteCustomer+props.delet.id;}
        jwtAxios.delete(url)
        .then(res=>{
            handleRes("true");
            console.log("נמחק");
        })
        .catch(error=>{
            console.log("שגיאה במחיקה");
            console.log(error.response.data.description);
            handleRes(error.response.data.description);
          });
        
    }
    
    
    
    const [err,setErr]=useState("");

    const handleClose = () =>{
        props.func(false);
    }
    
    const handleRes = (res:string) =>{
        if (res==="true"){
            if (store.getState().authState.role==="company"){UpdateUserCoupons();}
            else {UpdateStoreCoupon();
                  GetAllCompanies();}
            if (props.delet.lastName) {GetAllCoustomer()}    
            
            handleClose();
        } else {setErr(res);};
    }

    
    return (
        <div className="ConfirmDelete PoppingForm">
			Are you sure you want to delete {props.delet.title} {props.delet.name}? <br/>
            <Button className='PoppingFormButton' onClick={handleClose}>Close</Button>
            <Button className='PoppingFormButton' onClick={handleDelete}>Confirm</Button>
            <div>
            {err}
            </div>
        </div>
    );
}

export default ConfirmDelete;
