
import store from "../redux/Store";
import { Coupon } from "../models/Coupon";
import { cartDeleteAction, UpdateCartCouponErr } from "../redux/CartRedux";
import jwtAxios from "../../Utils/Interceptor";
import { RemoveSingleCouponsCoupon, setCouponErrAction } from "../redux/CouponRedux";
import { addUserCouponsAction, updateUserCouponsAction } from "../redux/AuthRedux";
import SetBought from "./SetBought";
import globals from "../../Utils/Globals";


function Purchase() : Coupon[] {
    
    const purchaseCoupons:Coupon[]=[];
        
    let updateDate= false;

    
    const IDs: number[]=[];
    store.getState().cartState.cartCoupons.map((item)=>IDs.push(item.id));
    jwtAxios.post( globals.customer.purchaseCoupon,IDs)
        .then((res) =>{ res.data.map((coupon:Coupon)=>{if (coupon.couponErr==="bought"){
                                                store.dispatch(cartDeleteAction(coupon));
                                                coupon.isBought=true;
                                                purchaseCoupons.push(coupon);
                                                store.dispatch(addUserCouponsAction(coupon));
                                                store.dispatch(RemoveSingleCouponsCoupon(coupon.id));
                                            } else{
                                                store.dispatch(UpdateCartCouponErr(coupon));
                                            }
                                            
                                            if (coupon.couponErr==="You already purchased the coupon, can't purchase"){
                                                updateDate=true;                    
                                            }
                                        }
                            )
                        }
                );
    
    if (updateDate as Boolean ===true){
        jwtAxios.get(globals.customer.getCoupons)
        .then((res) => {store.dispatch(updateUserCouponsAction(res.data))});
    }    
    SetBought();
   
        return(
            purchaseCoupons
        );
    
}
   
export default Purchase;