import { Coupon } from "../../models/Coupon";
import { cartAddAction, cartDeleteAction } from "../../redux/CartRedux";
import store from "../../redux/Store";
import "./SingleCoupon.css";
import * as React from 'react';
import { Dialog } from "@mui/material";
import CouponForm from "../../poppings/CouponForm/CouponForm";
import SuccessfullyAdded from "../../poppings/SuccessfullyAdded/SuccessfullyAdded";
import ConfirmDelete from "../../poppings/ConfirmDelete/ConfirmDelete";
import {  addToSingleCouponsAction, RemoveSingleCouponsCoupon } from "../../redux/CouponRedux";

interface SingleCouponProps {
    couponData:Coupon;
}

function SingleCoupon(props: SingleCouponProps): JSX.Element {
        var inCart = store.getState().cartState.cartCoupons.filter(coupon=>coupon.id==props.couponData.id).length>0;
        var updateCouponForm = false; 
        var updatedMessage= false;
        var myTitle = "";
        var deleteConfirmation= false;
        
       
        function dispatchCoupon(){
            store.dispatch(addToSingleCouponsAction({coupon:generateSingleCoupon(),couponID:props.couponData.id}))
        }

        const purch = ()=>{
            store.dispatch(cartAddAction(props.couponData));
            inCart = true;
            dispatchCoupon();
        }
    
        const remove = ()=>{
            store.dispatch(cartDeleteAction(props.couponData));
            inCart = false;
            dispatchCoupon()
        }
    
        const deleteCoupon = ()=>{
            deleteConfirmation= true;
            dispatchCoupon()
            
        }
    
        const updateCoupon = ()=>{
            updateCouponForm =true;
            dispatchCoupon()
        }
    
        const updatedSuccessfully = (title:string)=>{
            updateCouponForm= false ;
            myTitle = title;
            updatedMessage = true;
            store.dispatch(RemoveSingleCouponsCoupon(props.couponData.id));
            
        }

    function generateSingleCoupon():JSX.Element{
        return(
        <div className="SingleCoupon" id="coupon">
            <div className="Image"><img alt="not found" src={props.couponData.image} width={150} height={50}/></div>
            <div className="Title">{props.couponData.title}</div>
            <div className="Price">{props.couponData.price.toFixed(2)}</div>
            <div className="Description">{props.couponData.description}</div>
            <div className="Category">{props.couponData.category}</div>
            {!inCart && !props.couponData.isBought &&(store.getState().authState.role==="guest" || store.getState().authState.role==="customer") && <div className="Purchase" onClick={purch}> <button className="PurchaseButton">Add to Cart</button></div>}
            {inCart && (store.getState().authState.role==="guest" || store.getState().authState.role==="customer") && <div className="RemoveFromCart" onClick={remove}> <button className="RemoveButton">Remove</button></div>}
            {store.getState().authState.role==="company" && <div className="RemoveFromCart"><button className="DeleteButton" onClick={deleteCoupon}>Delete</button></div>}
            <div className="Last">{(store.getState().authState.role==="company" && <button className="UpdateButton" onClick={updateCoupon}>Update</button>) || (props.couponData.isBought&&"Already bought") || (props.couponData.amount<5?(props.couponData.amount>0?"Last Ones":"Out of Stock"):"")}</div>
            <Dialog disableEnforceFocus={true} open={updateCouponForm} onClose={()=>{updateCouponForm =false
                                                                                    dispatchCoupon()}}><CouponForm func={updatedSuccessfully} coupon={props.couponData}/></Dialog>
            <Dialog disableEnforceFocus={true} open={updatedMessage} onClose={()=>{updatedMessage = false}}><SuccessfullyAdded func={(set:boolean)=>updatedMessage = set} title={myTitle}/></Dialog>
            <Dialog disableEnforceFocus={true} open={deleteConfirmation}><ConfirmDelete func={(setTo:boolean)=>{deleteConfirmation=setTo
                                                                                                                dispatchCoupon()}} delet={props.couponData}/></Dialog>
        </div>
        )
    }
    var JSXCoupon = store.getState().couponState.singleCoupons.get(props.couponData.id);
    if (JSXCoupon===undefined ){
        JSXCoupon = generateSingleCoupon();
        dispatchCoupon();
    }

    return <div>{JSXCoupon}</div>
}

export default SingleCoupon;
