import "./AllCoupons.css";
import SingleCoupon from "../../modelsUI/SingleCoupon/SingleCoupon";
import { useEffect, useState } from "react";
import store from "../../redux/Store";
import FilterBar from "./FilterBar/FilterBar";


function AllCoupons(): JSX.Element {

    const [filters,setFilters] = useState(store.getState().couponState.filters);
    const [coupons,setCoupons] = useState(store.getState().couponState.coupons);
    useEffect(()=>{
        const unSub1 = store.subscribe(()=>{setCoupons(store.getState().couponState.coupons)});
        const unSub2 = store.subscribe(()=>{setFilters(store.getState().couponState.filters)});
        return ()=> {
            unSub1();
            unSub2();
        };
    },[])
    
    

    return (
        <div className="AllCoupons">
            <div className="FilterBar"><FilterBar/></div>
			<div className="CouponContainer">
                {coupons.filter(coupon=>coupon.title.toLowerCase().includes(filters.text.toLowerCase())||
                coupon.description.toLowerCase().includes(filters.text.toLowerCase())).filter(coupon=> (filters.categories.includes(coupon.category) && coupon.price>=filters.minPrice && (filters.maxPrice===0 || coupon.price<=filters.maxPrice)))
                .map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>)}
            </div>
        </div>
    );
}

export default AllCoupons;
