import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import FaceIcon from '@mui/icons-material/Face';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useState } from 'react';
import { loginAction, setOpenAction } from "../../redux/AuthRedux";
import {useForm,SubmitHandler} from "react-hook-form";
import store from '../../redux/Store';
import { Autocomplete, Dialog } from '@mui/material';
import "../PoppingForm.css";
import { couponsUpdateAction, resetSingleCouponsAction } from '../../redux/CouponRedux';
import GetAllCompanies from '../../adminActions/GetAllCompanies';
import { grey } from '@mui/material/colors';
import jwtAxios from '../../../Utils/Interceptor';
import SetBought from '../../customerAction/SetBought';
import Registr from '../RegisterForm/RegisterForm';
import { Link, } from 'react-router-dom';
import GetAllCoustomer from '../../adminActions/GetAllCustomers';
import UpdateStoreCoupon from '../../coupons/UpdateStoreCoupon/UpdateStoreCoupon';
import UpdateUserCoupons from '../../coupons/UpdateUserCoupons/UpdateUserCoupons';
import globals from '../../../Utils/Globals';


interface formable{
  email:string;
  password:string;
  role:string;
}

export function postLogin(){
  store.dispatch(resetSingleCouponsAction());
  console.log(store.getState().authState.role)
  store.getState().authState.role==="company"? UpdateUserCoupons(): UpdateStoreCoupon();
  if (store.getState().authState.role==="admin") {
    GetAllCompanies();
    GetAllCoustomer();
  }
  if (store.getState().authState.role==="customer") {
    SetBought();
  }
}

function Login(): JSX.Element {

  const {register,handleSubmit,formState:{errors}} = useForm<formable>();
  const theme = createTheme();
  const [serverError,setServerError]=useState("");
  const userId= store.getState().authState.id;
  

  const handelClose = ()=>{
    store.dispatch(setOpenAction(false));
  }
  

  
  const onSubmit:SubmitHandler<formable> = (data) => {
    const url = globals.admin.login+data.role+"/fullLogin";
    const senddata={email: data.email, password: data.password};
    jwtAxios.post(url,senddata)
    .then(res=>{
        store.dispatch(loginAction((res.data)));
        postLogin();
        window.location.reload();

        console.log("sucsscceful loged in, new token" + store.getState().authState.jwt);
    })
    .catch(error=>{
      console.log(error.response.status);
      console.log(error.response.data.description);
      setServerError(error.response.data.description);
    });
  
  };


  return (
    <ThemeProvider theme={theme}>
          <Box className='LoginForm PoppingForm'>
          <Avatar>
            <FaceIcon sx={{fontSize: 30, color: grey}}/>
          </Avatar>
          <Typography sx={{fontSize: 30}}>Sign in</Typography>
            <Box component="form"  onSubmit={handleSubmit(onSubmit)}>
              <div style={{color:"red"}}>
                {userId===-1&& "You have not used the system for a long time, please reconnect"}
                {userId===-2&& "You must log in first"}
                {userId===-3&& "You have successfully registered! \n Please sign in"}
              </div>
              <TextField
                className='PoppingFormText'
                margin="normal"
                required
                fullWidth
                id="email"
                label="Email Address"
                autoComplete="email"
                defaultValue={store.getState().authState.email}
                {...register("email",{required:true})}
              />
              <TextField
                className='PoppingFormText'
                margin="normal"
                required
                fullWidth
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
                variant="outlined" {...register("password",{required:true})}
              />
              <Autocomplete
                className='PoppingFormText'
                fullWidth
                disableClearable
                options={[
                  {label: 'admin'},
                  {label: 'company'},
                  {label: 'customer'}]}
                id="role"
                renderInput={(params) => <TextField {...params} required label="Role" {...register("role",{required:true})}/>}
              />
              
              <Button variant="contained" type="submit" fullWidth className='LoginFormSubmit'>Sign In</Button><br/>
              Don't have an account?<br/>
              <Link to="/register" style={{textDecoration:'none'}}><Button variant="contained" onClick={handelClose} fullWidth>Sign Up</Button></Link>
              
              <div style={{color:"red"}}>
                {serverError}
              </div>
            </Box>
          </Box>
    </ThemeProvider>
  );
  
}

export default Login;



