import React, { useEffect, useState } from "react";
import Dialog from "@material-ui/core/Dialog";
import Login from "../../poppings/LoginForm/LoginForm";
import store from "../../redux/Store";
import LoginIcon from '@mui/icons-material/Login';
import "./LoginButton.css";
import { setOpenAction } from "../../redux/AuthRedux";

function LoginButton(): JSX.Element {
  const [open,setOpen] = React.useState(store.getState().authState.isOpen);
  useEffect(()=>{
    const unSub = store.subscribe(()=>setOpen((store.getState().authState.isOpen)))
    return unSub
  },[])

  
  const handleClickToOpen = () => {
    store.dispatch(setOpenAction(true));
  };
  
  const handleToClose = () => {
    store.dispatch(setOpenAction(false));
  };
  
  return (
    
    <div className="LoginButton">
      <button className="TheButton" onClick={handleClickToOpen}>
        <div className="LoginText">Login</div>
        <div className="LoginIcon"><LoginIcon fontSize="large"/></div>
      </button>
      
      <Dialog open={open} onClose={handleToClose}>
        {/* <DialogActions>
          <Button onClick={handleToClose} color="primary"><CloseIcon fontSize="large"/></Button>
        </DialogActions>*/} 
        <Login/>
        
      </Dialog>  
    </div>
   
  );
}

export default LoginButton;