import { Global } from "@emotion/react";
import globals from "../../Utils/Globals";
import jwtAxios from "../../Utils/Interceptor";
import Company from "../models/Company";
import { getCompaniesAction } from "../redux/AdminRedux";
import store from "../redux/Store";




function GetAllCompanies()  {
    var companies:Company[] = [];
    jwtAxios.get<Company[]>(globals.admin.allCompanies).then(response=>{
        companies = response.data;
        console.log(companies);
        companies.map(company=>{company.coupons=
        store.getState().couponState.coupons.filter(coupon=>coupon.companyID==company.id)})
        store.dispatch(getCompaniesAction(companies));
    })
    .catch(err=>{
        console.log(err);
    });
}
    


export default GetAllCompanies;
