import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {useForm,SubmitHandler} from "react-hook-form";
import "../PoppingForm.css";
import store from '../../redux/Store';
import { updateUserDetailsAction } from '../../redux/AuthRedux';
import FaceIcon from '@mui/icons-material/Face';
import jwtAxios from '../../../Utils/Interceptor';
import { useState } from 'react';
import GetAllCoustomer from '../../adminActions/GetAllCustomers';
import globals from '../../../Utils/Globals';

interface formable{
  name:string;
  firstName:string;
  lastName:string;
  email:string;
  id?:number;
}



    
function ProfileForm(userCred?:any): JSX.Element{
  const [servRes, setServRes]=useState("");
  const role=store.getState().authState.role;
  userCred && console.log(userCred);
    
  const {register,handleSubmit,formState:{errors}} = useForm<formable>();
  const theme = createTheme();
  
  const successful = (data:any) => {
    console.log("פרטים עודכנו בהצלחה");
    setServRes("פרטים עודכנו בהצלחה");
    store.getState().authState.role!="admin" ? store.dispatch(updateUserDetailsAction(data)) : GetAllCoustomer();
  }

  const failed = (err: string) => {
    console.log("נכשל לעדכן פרטים");
    setServRes(err);
  }

  const onSubmit:SubmitHandler<formable> = (data) => {
      data.name=data.firstName;
      let url=globals.general.updateDetails+role+"/updateDetails";
      if (role==="admin"){ url=globals.admin.updateCustomer;
      data.id=userCred.userCred.id;}
      console.log(data);
      jwtAxios.put(url,data).then((res)=>{res.status===202?successful(data):failed("נכשל")})
      .catch(err=>{failed(err.response.data.description)})
      }

  return (
    <ThemeProvider theme={theme}>
          <Box className='ProfileForm PoppingForm'>
          <Avatar>
            <FaceIcon/>
          </Avatar>
          <Typography component="h1" variant="h5">{"Updating User Details"}</Typography>
            <Box className='PoppingForm' component="form"  onSubmit={handleSubmit(onSubmit)}>
              <TextField
                className='PoppingFormText'
                required
                fullWidth
                id="firstName"
                label="First Name"
                defaultValue={userCred.userCred? userCred.userCred.firstName : store.getState().authState.name}
                variant="outlined" {...register("firstName",{maxLength:15, required:true})}
              />
              {role!="company" && <TextField
                className='PoppingFormText'
                margin="normal"
                fullWidth
                label="Last name"
                id="lastName"
                defaultValue={userCred.userCred? userCred.userCred.lastName :store.getState().authState.lastName}
                variant="outlined" {...register("lastName",{maxLength:15, required:true})}
              />}
              <br/>
              <TextField
                className='PoppingFormText'
                margin="normal"
                required
                fullWidth
                label="Email"
                id="email"
                defaultValue={userCred.userCred? userCred.userCred.email: store.getState().authState.email}
                variant="outlined" {...register("email",{maxLength:50, required:true})}
              />
              <br/>
              <Button className='PoppingFormButton' variant="contained" type="submit">{"Update Details"}</Button>
              {(errors.firstName && "name max length 15") || (errors.lastName && "last name max length 15")
                || (errors.email && "email max length 50")}
              {servRes}
            </Box>
          </Box>
    </ThemeProvider>
  );
}

export default ProfileForm;