import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import PasswordIcon from '@mui/icons-material/Password';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {useForm,SubmitHandler} from "react-hook-form";
import { useState } from 'react';
import "../PoppingForm.css";
import store from '../../redux/Store';
import jwtAxios from '../../../Utils/Interceptor';
import globals from '../../../Utils/Globals';

interface formable{
  oldPassword:string;
  newPassword:string;
  newPasswordAgain:string;
}



    
function ChangePassword(): JSX.Element{
  const [equal,setEqual] = useState("");
  const {register,handleSubmit,formState:{errors}} = useForm<formable>();
  const theme = createTheme();
  
  const successful = () => {
    console.log("סיסמא עודכנה בהצלחה");
    setEqual("סיסמא עודכנה בהצלחה");
  }

  const failed = () => {
    console.log("נכשל לעדכן סיסמא");
  }

  const onSubmit:SubmitHandler<formable> = (data) => {
      if (data.newPassword===data.newPasswordAgain){
        setEqual("");
        const role=store.getState().authState.role;
        const url=globals.general.updatePassword+role+"/updatePassword";
        jwtAxios.put(url,data).then((res)=>{res.status===202?successful():failed()})
        .catch(err=>setEqual(err.response.data.description));
        }else {setEqual("The passwords are not the same");}
      }

  return (
    <ThemeProvider theme={theme}>
          <Box className='ChangePassword PoppingForm'>
          <Avatar>
            <PasswordIcon/>
          </Avatar>
          <Typography component="h1" variant="h5">{"Change password"}</Typography>
            <Box component="form"  onSubmit={handleSubmit(onSubmit)}>
              <TextField
                className='PoppingFormText'
                required
                style={{width: 300, height:50,margin:10}}
                id="oldPassword"
                type="password"
                label="Old Password"
                variant="outlined" {...register("oldPassword",{required:true})}
              />
              <br/>
              <TextField
                className='PoppingFormText'
                margin="normal"
                style={{width: 300, height:50,margin:10}}
                type="password"
                label="New Password"
                id="new Password"
                variant="outlined" {...register("newPassword",{required:true})}
              />
              <br/>
              <TextField
                className='PoppingFormText'
                margin="normal"
                required
                type="password"
                style={{width: 300, height:50,margin:10}}
                label="New Password again"
                id="newPasswordAgain"
                variant="outlined" {...register("newPasswordAgain",{required:true})}
              />
              <br/>
              <div>{equal}</div>
              <Button variant="contained" type="submit" style={{width: 300, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{"Update Password"}</Button>
            </Box>
          </Box>
    </ThemeProvider>
  );
}

export default ChangePassword;