import axios from "axios";
import globals from "../../../Utils/Globals";
import { couponsUpdateAction } from "../../redux/CouponRedux";
import store from "../../redux/Store";
import "./UpdateStoreCoupon.css";

function UpdateStoreCoupon() {
    if (store.getState().authState.role ==="company"){
        store.dispatch(couponsUpdateAction(store.getState().authState.coupons));
    } else{
        axios.get(globals.general.allCoupons)
        .then(response=>{store.dispatch(couponsUpdateAction(response.data));})
        .catch(err=>{
            console.log(err);
        });
    }
}

export default UpdateStoreCoupon;
