import "./Bag.css";
import CartIcon from '@mui/icons-material/ShoppingCart';
import store from "../../redux/Store";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";

function Bag(): JSX.Element {
    const [cartCounter,setCartCounter] = useState(0);
    useEffect(()=>{const unSubFunc = store.subscribe(()=>setCartCounter(store.getState().cartState.cartCoupons.length))
        return (unSubFunc) //unSubFunc will be invoked when "Bag" will unmount.
    }
,[]);
    
    return (
        <div className="Bag">
            <Link to="/mycart" className="MyCartButton">
                <div className="MyCartTitle">My Cart</div>
                <div className="CartIcon"><CartIcon fontSize="large"/></div>
                <div id="PurchaseCounter">{cartCounter}</div>
            </Link>
    </div>
    );
}

export default Bag;
