import { Box, Button, createTheme, TextField, ThemeProvider, Typography } from "@mui/material";
import { Method } from "axios";
import React from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import globals from "../../../Utils/Globals";
import jwtAxios from "../../../Utils/Interceptor";
import GetAllCompanies from "../../adminActions/GetAllCompanies";
import Company from "../../models/Company";
import "../PoppingForm.css";

interface formable{
    email:string;
    name:string;
    password:string
    id?:number;
}


interface myProps{
    company?:Company;
    func:Function;
  }
  

function CompanyForm(props:myProps): JSX.Element {

    const {register,handleSubmit,formState:{errors}} = useForm<formable>();
    const theme = createTheme();
    const [serverError, setServerError]=React.useState<String>("");


    const onSubmit:SubmitHandler<formable> = (data) => {
      console.log(data);
      let axiosType:Method="post";
      let url=globals.admin.addCompany;  
      if (props.company) {
          data.id=props.company.id;
          url=globals.admin.updateCompany;
          axiosType="put"}
      jwtAxios({method:axiosType, url:url, data:data}).then(res=>{
            setServerError("עודכן בהצלחה");
            console.log("עודכן בהצלחה");
            props.func(data.name);
            GetAllCompanies();
          })
          .catch(error=>{
            console.log(error.response);
            setServerError(error.response.data.description);        
          });
          
      } 
      


    return (
        <ThemeProvider theme={theme}>
          <Box className="CompanyForm PoppingForm">
          <Typography component="h1" variant="h5">{props.company?"Updating Company":"Adding Company"}</Typography>
            <Box component="form"  onSubmit={handleSubmit(onSubmit)}> 
            <TextField
                className='PoppingFormText'
                required
                style={{width: 200, height:50,margin:10}}
                id="name"
                label="Name"
                defaultValue={props.company?props.company.name:""}
                variant="outlined" {...register("name",{maxLength:20, required:true })}
              />
            <br/>
              <TextField
                className='PoppingFormText'
                margin="normal"
                style={{width:200,height:50,margin:10}}
                label="Email"
                id="email"
                defaultValue={props.company?props.company.email:""}
                variant="outlined" {...register("email",{required:true})}
              />
              <br/>
              <TextField
                className='PoppingFormText'
                style={{width: 200, height:50,margin:10}}
                id="password"
                label="Password"
                type="password"
                variant="outlined" {...register("password",{maxLength:25 })}
              />
              <br/>
              <Button variant="contained" type="submit" style={{width: 140, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{props.company?"Update Company":"Add Company"}</Button>
              <div style={{color:"red"}}>
                {(errors.name && "name max length 20") || (errors.password && "PASSWORD max length 20") }
                {serverError} 
              </div>
            </Box>
          </Box>
        </ThemeProvider>
    );
}

export default CompanyForm;
