import ShiriImage from "../../../Assets/SmrtLogo.png";
import MatanImage from "../../../Assets/SmrtLogo.png";
import RoeiImage from "../../../Assets/SmrtLogo.png";
import TalImage from "../../../Assets/SmrtLogo.png";
import "./AboutUs.css";

function AboutUs(): JSX.Element {
    const ShiriPage = ():JSX.Element => {
        const image = <img alt="not found" src={ShiriImage}></img>;
        const name = "Shiri Levi";
        const desc = "A lovely young lady with beautiful eyes, with a great work ethic and managing skills of a lawyer in the making";
        return CreateOnePage(name,desc,image);
    }

    const MatanPage = ():JSX.Element => {
        const image = <img alt="not found" src={MatanImage}></img>;
        const name = "Matan Ozer";
        const desc = "A brilliant young man that will always think big due to constant curiosity";
        return CreateOnePage(name,desc,image);
    }
    
    const RoeiPage = ():JSX.Element => {
        const image = <img alt="not found" src={RoeiImage}></img>;
        const name = "Roei Rashty";
        const desc = "A super young boy who is always looking for perfection and will never compromise";
        return CreateOnePage(name,desc,image);
    }
    
    const TalPage = ():JSX.Element => {
        const image = <img alt="not found" src={TalImage}></img>;
        const name = "Tal Harel";
        const desc = "An experienced and wise man, who always knows what to do and how to solve problems";
        return CreateOnePage(name,desc,image);
    }

    const CreateOnePage = (name:String,desc:String,image:JSX.Element):JSX.Element => {
        return (
            <div className="EntrepreneurPage">
                <div className="EntrepreneurImage">{image}</div>
                <div className="EntrepreneurName">{name}</div>
                <div className="EntrepreneurDesc">{desc}</div>
            </div>
        )
    }

    return (
        <div className="AboutUs">
			SMRT Coupons is a fake company which established for a programming studies project.<br/><br/>
            The entrepreneurs of SMRT Coupons are:<br/>
            <div className="EntrepreneursContainer">
                {ShiriPage()}
                {MatanPage()}
                {RoeiPage()}
                {TalPage()}
            </div>
        </div>
    );
}

export default AboutUs;
