import { Coupon } from "../models/Coupon";

export class CartState{
    public cartCoupons:Coupon[] = [];
}


export enum CartActionType {
    UpdateCart = "UpdateCart",
    DeleteFromCart = "DeleteFromCart",
    AddToCart = "AddToCart",
    ClearCart = "ClearCart",
    UpdateCartFromLocal = "UpdateCartFromLocal",
    UpdateCouponErr = "UpdateCouponErr"
}

export interface CartAction{
    type: CartActionType,
    payload?:any,
}

export function cartUpdateAction(coupons:Coupon[]):CartAction{
    return {type: CartActionType.UpdateCart,payload:coupons}
}

export function cartDeleteAction(coupon:Coupon):CartAction{
    return {type: CartActionType.DeleteFromCart,payload:coupon}
}

export function cartAddAction(coupon:Coupon):CartAction{
    return {type: CartActionType.AddToCart,payload:coupon}
}

export function cartClearAction():CartAction{
    return {type: CartActionType.ClearCart}
}

export function UpdateCartFromLocalAction(cartState:CartState):CartAction{
    return {type: CartActionType.UpdateCartFromLocal, payload: cartState}
}

export function UpdateCartCouponErr(coupon:Coupon):CartAction{
    return {type: CartActionType.UpdateCouponErr, payload: coupon}
}

//reducer
export function cartReducer(currentState: CartState = new CartState, action: CartAction):CartState{
    let newState = {...currentState};
    switch(action.type){
        case CartActionType.UpdateCart:
            newState.cartCoupons=action.payload;
            localStorage.setItem("CartState",JSON.stringify(newState));
            break;
        case CartActionType.AddToCart:
            newState.cartCoupons.push(action.payload);
            localStorage.setItem("CartState",JSON.stringify(newState));
            break;
        case CartActionType.DeleteFromCart:
            newState.cartCoupons = newState.cartCoupons.filter(item => item.id!==action.payload.id);
            localStorage.setItem("CartState",JSON.stringify(newState));
            break;
        case CartActionType.ClearCart:
            newState.cartCoupons = [];
            localStorage.setItem("CartState",JSON.stringify(newState));
            break;
        case CartActionType.UpdateCartFromLocal:
            newState=action.payload;
            break;
        case CartActionType.UpdateCouponErr:
            newState.cartCoupons.map(coupon=>
                {if (coupon.id===action.payload.id)
                    coupon.couponErr=action.payload.couponErr;})
            localStorage.setItem("CartState",JSON.stringify(newState));
            break;
    }
    return newState;
}