import { useEffect } from "react";
import globals from "../../Utils/Globals";
import jwtAxios from "../../Utils/Interceptor";
import { setCastomersAction } from "../redux/AdminRedux";
import store from "../redux/Store";


function GetAllCoustomer()  {
    const companies=store.getState().adminState.companies;
    const url =globals.admin.allCustomers;

    jwtAxios.get(url).then(res=>{
            store.dispatch(setCastomersAction(res.data))
        })
    .catch(err=>console.log(err));
}
    
export default GetAllCoustomer;
