import axios from "axios";
import { request } from "http";
import store from "../Components/redux/Store";
import { forceLogoutAction, userUpdateAction } from "../Components/redux/AuthRedux";

const jwtAxios = axios.create();


//request interceptor - מה אנו רוצים לבצע בכל שליחת בקשה לשרת
jwtAxios.interceptors.request.use(request=>{
    request.headers={"authorization":store.getState().authState.jwt}
    return request;
});


jwtAxios.interceptors.response.use(response=>{
    if((response.headers.authorization)!=null){
        console.log("new token from axios interceptor");
        console.log(response.headers.authorization);
        store.dispatch(userUpdateAction(response.headers.authorization));
    };     
    return response;
},  (error)=> {
    console.log("error in the response");
    if (error.response.data.error=="Token Error"){
        store.dispatch(forceLogoutAction());
        console.log("force logout");
    }
    return Promise.reject(error);
  });



export default jwtAxios;




