import { useEffect, useState } from "react";
import UpdateStoreCoupon from "../../coupons/UpdateStoreCoupon/UpdateStoreCoupon";
import SingleCoupon from "../../modelsUI/SingleCoupon/SingleCoupon";
import store from "../../redux/Store";
import "./HomePage.css";

function HomePage(): JSX.Element {
   
    
    
    const [search,setSearch] = useState("");
    const setValue = (args:React.SyntheticEvent)=>{
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
    };
    
    const coupons = store.getState().couponState.coupons;

    const getLastAmountCoupon=()=>{
        return coupons.filter(coupon=>coupon.title.toLowerCase().includes(search.toLowerCase())||
        coupon.description.toLowerCase().includes(search.toLowerCase()))
        .filter(item=> item.amount<5)
        .map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>);
    }

    const getLastDaysCoupon=()=>{
        const date = new Date();
        return coupons.filter(coupon=>coupon.title.toLowerCase().includes(search.toLowerCase())||
        coupon.description.toLowerCase().includes(search.toLowerCase()))
        .filter(item=> (Number)(item.endDate.substring(5,7))===(date.getMonth()+1) && (Number)(item.endDate.substring(8,10)) < (date.getDate()+5))
        .map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>);
    }

    const getNewestCoupon=()=>{
        const date = new Date();
        return coupons.filter(coupon=>coupon.title.toLowerCase().includes(search.toLowerCase())||
        coupon.description.toLowerCase().includes(search.toLowerCase()))
        .filter(item=> (Number)(item.startDate.substring(5,7))===(date.getMonth()+1) && (Number)(item.startDate.substring(8,10)) > (date.getDate()-5))
        .map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>);
    }

    return (
        <div className="HomePage">
            <div className="Bar">
                <div className="BarTitle">Last Ones:</div>
			    <div className="CouponBar">{getLastAmountCoupon()}</div>
            </div>
            <div className="Bar">
                <div className="BarTitle">Last Days:</div>
			    <div className="CouponBar">{getLastDaysCoupon()}</div>
            </div>
            <div className="Bar">
                <div className="BarTitle">Newest Coupons:</div>
			    <div className="CouponBar">{getNewestCoupon()}</div>
            </div>
        </div>
    );
}

export default HomePage;
