
import "../PoppingForm.css";
import { useEffect, useState } from "react";
import store from "../../redux/Store";
import { Button } from "@mui/material";
import SingleCompany from "../../modelsUI/SingleCompany/SingleCompany";

interface myProps{
    name:string;
    func:Function;
}

function SuccessfullyAddedCompany(props:myProps): JSX.Element {
    const handelClose =()=>{
        props.func(false);
    };
    const [companies,setCompanies]=useState(store.getState().adminState.companies);
    

    useEffect(()=>{
        const unSub = store.subscribe(()=>setCompanies(store.getState().adminState.companies));
        return unSub;
    },[]);
    
    return (
            <div className="SuccessfullyAdded PoppingForm">
                <div className="Congrats"> {"company "+ props.name+ " added"}</div>
                {companies.filter(item=> item.name === props.name)
                .map((item)=>{return <span key={item.id}><SingleCompany companyData={item}/></span>})}
                <br/>
                <Button className='PoppingFormButton' onClick={handelClose}>Close</Button>
            </div>
    );
    
}

export default SuccessfullyAddedCompany;
