class Globals{
}

class DevelopmentGlobals extends Globals{
    public general= {
        allCoupons : "http://localhost:8080/allCoupons",
        register : "http://localhost:8080/newCustomer",
        updatePassword : "http://localhost:8080/",
        updateDetails : "http://localhost:8080/",
        hrefRegister : "http://localhost:3000/register",
    };

    public admin = {
        login : "http://localhost:8080/",
        allCompanies : "http://localhost:8080/admin/allCompanies",
        allCustomers : "http://localhost:8080/admin/allCustomers",
        addCompany : "http://localhost:8080/admin/addCompany",
        updateCompany : "http://localhost:8080/admin/updateCompany",
        deleteCoupon : "http://localhost:8080/company/deleteCoupon/",
        deleteCompany : "http://localhost:8080/admin/deleteCompany/",
        deleteCustomer : "http://localhost:8080/admin/deleteCustomer/",
        updateCustomer : "http://localhost:8080/admin/updateCustomer",
   }

   public company = {
       allCoupons : "http://localhost:8080/company/allCoupons",
       addCoupon : "http://localhost:8080/company/addCoupon",
       updateCoupon : "http://localhost:8080/company/updateCoupon",
   }

   public customer = {
        purchaseCoupon : "http://localhost:8080/customer/purchaseSomeCoupons",
        getCoupons : "http://localhost:8080/customer/customerCoupons",
   }
}

class ProductionGlobals extends Globals{
    public general = {
        allCoupons : "/allCoupons",
        register : "/newCustomer",
        updatePassword : "/",
        updateDetails : "/",
        hrefRegister : "/register",
    };

    public admin = {
        login : "/",
        allCompanies : "/admin/allCompanies",
        allCustomers : "/admin/allCustomers",
        addCompany : "/admin/addCompany",
        updateCompany : "/admin/updateCompany",
        deleteCoupon : "/company/deleteCoupon/",
        deleteCompany : "/admin/deleteCompany/",
        deleteCustomer : "/admin/deleteCustomer/",
        updateCustomer : "/admin/updateCustomer",
   };

   public company = {
        allCoupons : "/company/allCoupons",
        addCoupon : "/company/addCoupon",
        updateCoupon : "/company/updateCoupon",

   }

   public customer = {
        purchaseCoupon : "customer/purchaseSomeCoupons",
        getCoupons : "/customer/customerCoupons",
   }
}

const globals = process.env.NODE_ENV === 'production' ? new ProductionGlobals : new DevelopmentGlobals;

export default globals;