import { Coupon } from "../models/Coupon";


export class Filters {
    minPrice:Number = 0;
    maxPrice:Number = 100;
    categories:String[] = ["ELECTRICITY","RESTAURANT","FASHION","GROCERY","VACATION"];
    text:String = "";
}
export interface singleCouponHelper{
    coupon:JSX.Element;
    couponID:number;
    }

export class CouponState{
    public coupons:Coupon[] = [];
    public filters:Filters = new Filters();
    public highestPrice:Number = 100;
    public singleCoupons:Map<Number,JSX.Element> = new Map<Number,JSX.Element>();
}

export enum CouponsActionType{
    UpdateCoupons = "UpdateCoupons",
    DeleteCoupon = "DeleteCoupon",
    AddCoupon = "AddCoupon",
    SetFilters = "SetFilters",
    UpdateCouponErr = "UpdateCouponErr",
    AddToSingleCoupons = "AddToSingleCoupons",
    ResetSingleCoupons = "ResetSingleCoupons",
    RemoveSingleCouponsCoupon = "RemoveSingleCouponsCoupon",
}

export interface CouponsAction{
    type: CouponsActionType,
    payload?:any,
}

export function couponsUpdateAction(coupons:Coupon[]):CouponsAction{
    return {type: CouponsActionType.UpdateCoupons,payload:coupons}
}

export function couponDeleteAction(coupon:Coupon):CouponsAction{
    return {type: CouponsActionType.DeleteCoupon,payload:coupon}
}

export function couponAddingAction(coupon:Coupon):CouponsAction{
    return {type: CouponsActionType.AddCoupon,payload:coupon}
}


export function setFiltersAction(filters:Filters):CouponsAction{
    return {type: CouponsActionType.SetFilters,payload:filters}
}

export function setCouponErrAction(coupon:Coupon):CouponsAction{
    return {type: CouponsActionType.UpdateCouponErr,payload:coupon}
}
export function addToSingleCouponsAction(coupon:singleCouponHelper):CouponsAction{
    return {type: CouponsActionType.AddToSingleCoupons,payload:coupon}
}
export function resetSingleCouponsAction():CouponsAction{
    return {type: CouponsActionType.ResetSingleCoupons,payload:{}}
}
export function RemoveSingleCouponsCoupon(id:number):CouponsAction{
    return {type: CouponsActionType.RemoveSingleCouponsCoupon,payload:id}
}



//reducer
export function couponReducer(currentState: CouponState = new CouponState(), action: CouponsAction):CouponState{
    const newState = {...currentState};
    switch(action.type){
        case CouponsActionType.UpdateCoupons:
            newState.coupons=action.payload;
            const numbers:number[] = action.payload.map((coupon: { price: any; })=>coupon.price);
            newState.highestPrice=Math.max(...numbers);
            newState.singleCoupons = new Map<Number,JSX.Element>();
            break;
        case CouponsActionType.DeleteCoupon:
            newState.coupons = newState.coupons.filter(item=> item.id!==action.payload.id);
            if (action.payload.price === newState.highestPrice){
                const numbers:number[] = newState.coupons.map((coupon: { price: any; })=>coupon.price);
            newState.highestPrice=Math.max(...numbers);
            }
            break;
        case CouponsActionType.AddCoupon:
            newState.coupons.push(action.payload);
            newState.highestPrice = action.payload.price>newState.highestPrice?action.payload.price:newState.highestPrice;
            break;
        case CouponsActionType.SetFilters:
            newState.filters = action.payload;
            break;
        case CouponsActionType.UpdateCouponErr:
            newState.coupons.map(coupon=>
                {if (coupon.id==action.payload.id)
                     coupon.couponErr=action.payload.couponErr;})
            break;
            case CouponsActionType.AddToSingleCoupons:
                newState.singleCoupons.set(action.payload.couponID,action.payload.coupon);
                break;
            case CouponsActionType.ResetSingleCoupons:
                newState.coupons.map(coupon=>coupon.isBought==false);
                newState.singleCoupons = new Map<Number,JSX.Element>();
                break;
            case CouponsActionType.RemoveSingleCouponsCoupon:
                console.log(newState.singleCoupons.delete(action.payload)); 
                break;
    }
    return newState;
}