import { TextField } from "@mui/material";
import { useEffect, useState } from "react";
import store from "../../redux/Store";
import GetAllCoustomer from "../../adminActions/GetAllCustomers";
import SingleCustomer from "../../modelsUI/SingleCustomer/SingleCustomer";
import "./AllCoustomer.css";
import { useNavigate } from "react-router";






function AllCoustomer(): JSX.Element {
    const nav = useNavigate();
    useEffect(()=>{
        if (store.getState().authState.role!="admin"){nav("/");}
        store.getState().adminState.coustomers.length<1 && GetAllCoustomer();
    } ,[]);

    const [coustomers, setCoustomers]= useState(store.getState().adminState.coustomers);
    store.subscribe(()=>setCoustomers(store.getState().adminState.coustomers))

    
    const [search,setSearch] = useState("");
    const setValue = (args:React.SyntheticEvent)=>{
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
    };
    
    
  
    return (
  
    <div>
        <br/>
        <div >
         
             <TextField 
            type="text"  label="search customer" placeholder={"search customer"} onChange={setValue} value={search}
            variant="outlined"/>
        </div>
        <br/>

        <div className="AllCustomer">
            <div className="Container">
            {coustomers.filter(cus=>((cus.firstName+" "+cus.lastName).toLowerCase().includes(search.toLowerCase())||
            (cus.lastName+" "+cus.firstName).toLowerCase().includes(search.toLowerCase())||
            cus.email.toLowerCase().includes(search.toLowerCase())))
            .map(coustomer=><span key={coustomer.id}><SingleCustomer customerData={coustomer}/></span>)}
        </div>
        </div>
    </div>
    );
}


export default AllCoustomer;
function nav(arg0: string) {
    throw new Error("Function not implemented.");
}

