import "./Menu.css";
import { Link } from "react-router-dom";
import { Button, Dialog } from "@mui/material";
import CouponForm from "../../poppings/CouponForm/CouponForm";
import SuccessfullyAdded from "../../poppings/SuccessfullyAdded/SuccessfullyAdded";
import React, { useEffect } from "react";
import store from "../../redux/Store";
import { Filters, setFiltersAction } from "../../redux/CouponRedux";
import AllCompanies from "../../mainpages/AllCompanies/AllCompanies";
import CompanyForm from "../../poppings/CompanyForm/CompanyForm";
import SuccessfullyAddedCompany from "../../poppings/SuccessfullyAdded/SuccessfullyAddedCompany";



function Menu(): JSX.Element {
    const[addCouponForm,setAddCouponForm] = React.useState(false);
    const[successMessage,setSuccessMessage] = React.useState(false);
    const[successMessageComp,setSuccessMessageComp] = React.useState(false);
    const[addCompForm,setAddCompForm] = React.useState(false);
    const[title,setTitle] = React.useState("");
    const[name,setName] = React.useState("");
    const role=store.getState().authState.role;
  
    const changeDialog = (couponTitle:string) => {
      setAddCouponForm(false);
      setTitle(couponTitle);
      setSuccessMessage(true);
    }
    const changeDialogCompany = (companyName:string) => {
      setAddCompForm(false);
      setName(companyName);
      setSuccessMessageComp(true);
    }


    const updateFilter = (category:string) => {
      const filter = store.getState().couponState.filters;
      filter.categories = [category];
      console.log(filter.categories);
      if (category==="all") {
        filter.categories = ["ELECTRICITY","RESTAURANT","FASHION","GROCERY","VACATION"];
      }
      store.dispatch(setFiltersAction(filter));
    }

    // useEffect(()=>{
    //     store.subscribe(()=>);
    // },[]);

    return (
        <div className="Menu">
             <Dialog disableEnforceFocus={true} open={addCouponForm} onClose={()=>{setAddCouponForm(false)}}><CouponForm func={changeDialog}/></Dialog>
             <Dialog disableEnforceFocus={true} open={successMessage} onClose={()=>{setSuccessMessage(false)}}><SuccessfullyAdded title={title} func={setSuccessMessage}/></Dialog>
             <Dialog disableEnforceFocus={true} open={successMessageComp} onClose={()=>{setSuccessMessageComp(false)}}><SuccessfullyAddedCompany name={name} func={setSuccessMessageComp}/></Dialog>
             <Dialog disableEnforceFocus={true} open={addCompForm} onClose={()=>{setAddCompForm(false)}}><CompanyForm func={changeDialogCompany}/>
             <Button variant="contained" className="PoppingCloseButton" onClick={()=>{setAddCompForm(false)}}>{"close"}</Button></Dialog>
            <div className="container">
              {role==="company" && <button className="addCoupon action button" onClick={()=>{setAddCouponForm(true)}}>Add Coupon</button>}
              {role==="admin" && <Link className="allCompany action button" to="/allCompanies">All Companies</Link>}
              {role==="admin" && <Link className="allCompany action button" to="/allCoustomers">All Customers</Link>}
              {role==="admin" && <button className="addCompany action button" onClick={()=>{setAddCompForm(true)}}>Add Company</button>}
                <Link className="all button" onClick={()=>updateFilter("all")} to="/coupons">All Coupons</Link>
                <Link className="fashion button" onClick={()=>updateFilter("FASHION")} to="/coupons">Fashion</Link>
                <Link className="electricity button" onClick={()=>updateFilter("ELECTRICITY")} to="/coupons" >Electricity</Link>
                <Link className="vacation button" onClick={()=>updateFilter("VACATION")} to="/coupons" >Vacation</Link>
                <Link className="grocery button" onClick={()=>updateFilter("GROCERY")} to="/coupons">Grocery</Link>
                <Link className="restaurant button" onClick={()=>updateFilter("RESTAURANT")}  to="/coupons">Restaurant</Link>
               
            </div>
        </div>
    );
}

export default Menu;
