import store from "../../redux/Store";
import * as React from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { logoutAction } from "../../redux/AuthRedux";
import AccountIcon from '@mui/icons-material/AccountCircle';
import "./ProfileButton.css";
import { couponsUpdateAction, resetSingleCouponsAction, } from "../../redux/CouponRedux";
import Dialog from "@material-ui/core/Dialog";
import { Link, useNavigate } from "react-router-dom";
import { adminLogoutAction, } from "../../redux/AdminRedux";
import UpdateStoreCoupon from "../../coupons/UpdateStoreCoupon/UpdateStoreCoupon";


function ProfileButton(): JSX.Element {
  const [profileOpen,setProfileOpen] = React.useState(false);
  const nav = useNavigate();

  
  const handleToClose = () => {
    setProfileOpen(false);
  };
  
  
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  
  const handlelogout = ()=>{
    handleClose();
    store.dispatch(logoutAction());
    store.dispatch(resetSingleCouponsAction());
    store.dispatch(adminLogoutAction());
    UpdateStoreCoupon();
    nav("/");
  };

  const handleProfile = ()=>{
    handleClose();
    //setProfileOpen(true);
  };

  return (
    
    <div className="ProfileButton">
      <button
        className="TheButton"
        id="demo-positioned-button"
        aria-controls={open ? 'demo-positioned-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        <div className="ProfileText">My Account</div>
        <div className="PofileIcon"><AccountIcon fontSize="large"/></div>
      </button>
      <Menu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
      >
        <MenuItem onClick={handleProfile}>Profile</MenuItem>
        <Link to="/myaccount" style={{textDecoration: 'none', color: "black"}}><MenuItem onClick={handleClose}>My account</MenuItem></Link>
        <MenuItem onClick={handlelogout}>Logout</MenuItem>
      </Menu>
      <Dialog open={profileOpen} onClose={handleToClose}>
        
      </Dialog>
    </div>
    
  );
}

export default ProfileButton;

