import { SelectChangeEvent, FormControl, InputLabel, Select, OutlinedInput, MenuItem, Checkbox, ListItemText, Slider, TextField, FormLabel, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { Filters, setFiltersAction } from "../../../redux/CouponRedux";
import store from "../../../redux/Store";
import "./FilterBar.css";

function FilterBar(): JSX.Element {
    const categoryList = ['ELECTRICITY','FASHION','VACATION','RESTAURANT','GROCERY',];
    const [categories, setCategories] = React.useState<String[]>(store.getState().couponState.filters.categories);
    const [filterPrices, setFilterPrices] = React.useState<Number[]>([store.getState().couponState.filters.minPrice,store.getState().couponState.filters.maxPrice]);
    const [search,setSearch] = React.useState<String>(store.getState().couponState.filters.text);
    
    useEffect(()=>{
        setFilterPrices([0,store.getState().couponState.highestPrice]);
        const unSub1 = store.subscribe(()=>setCategories(store.getState().couponState.filters.categories));
        return ()=>{
            unSub1();
        }
    },[]);

    const handleTextChange = (args:React.SyntheticEvent)=>{
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
        const filter = store.getState().couponState.filters;
        filter.text = value;
        store.dispatch(setFiltersAction(filter));
    };
    
    const handleCategoryChange = (event: SelectChangeEvent<typeof categories>) => {
        const {target: { value },} = event;
        setCategories(
          typeof value === 'string' ? value.split(',') : value
        )
        const filter = store.getState().couponState.filters;
        filter.categories= (typeof value === 'string' ? value.split(',') : value);
        store.dispatch(setFiltersAction(filter));
    };

    const handlePriceChange = (event: Event, newValue: number | number[]) => {
        setFilterPrices(newValue as number[]);
        const filter = store.getState().couponState.filters;
        filter.minPrice=(Number)((newValue as number[]).at(0));
        filter.maxPrice=(Number)((newValue as number[]).at(1));
        store.dispatch(setFiltersAction(filter))
      };
    
    return (
	    <div className="FilterBar">
            <TextField className="FilterBarText" type="text" label="search coupon" placeholder={"search coupon"} onChange={handleTextChange} value={search} variant="outlined"/>
            <Select
                className="FilterBarText"
                labelId="demo-multiple-checkbox-label"
                id="demo-multiple-checkbox"
                multiple
                value={categories}
                onChange={handleCategoryChange}
                input={<OutlinedInput label="Category" />}
                renderValue={(selected) => selected.join(', ')}
            >
                {categoryList.map((name) => (
                    <MenuItem key={name} value={name}>
                        <Checkbox checked={categories.indexOf(name) > -1} />
                        <ListItemText primary={name} />
                    </MenuItem>
                ))}
            </Select>
            <Slider
                size="small"
                sx={{width: 500, ml: 2}}
                step={5}
                max = {store.getState().couponState.highestPrice as number}
                getAriaLabel={() => 'Temperature range'}
                value={filterPrices as number[]}
                onChange={handlePriceChange}
                valueLabelDisplay="on"
            />
        </div>
    );
}

export default FilterBar;
