import { Component } from "react";
import { BrowserRouter } from "react-router-dom";
import Footer from "../Footer/Footer";
import Header from "../Header/Header";
import "./Layout.css";
import Main from "../Main/Main";
import UpdateStoreCoupon from "../../coupons/UpdateStoreCoupon/UpdateStoreCoupon";
import store from "../../redux/Store";
import { UpdateFromLocalStorageAction } from "../../redux/AuthRedux";
import { UpdateCartFromLocalAction } from "../../redux/CartRedux";
import { postLogin } from "../../poppings/LoginForm/LoginForm";

class Layout extends Component {
    unSubFunc:Function = ()=>console.log("unsubfunction in layout is empty!");
    constructor(props:any) {
        super(props);
        this.state = {role: store.getState().authState.role};
      }

    componentDidMount(){
        UpdateStoreCoupon();
        this.unSubFunc = store.subscribe(()=>this.setState({role:store.getState().authState.role}));
        if (localStorage.getItem("AuthState")){
            store.dispatch(UpdateFromLocalStorageAction(JSON.parse(localStorage.getItem("AuthState") as string)));
            }
        if (localStorage.getItem("CartState")){
            store.dispatch(UpdateCartFromLocalAction(JSON.parse(localStorage.getItem("CartState") as string)));
            }
        postLogin(); 
    }

    componentWillUnmount(){
        this.unSubFunc();
    }
    
    public render(): JSX.Element {
       

        return (
            
            <div className="Layout">
                <BrowserRouter>
                    <header><Header/></header>
                    <main><Main/></main>
                    <footer><Footer/></footer>
                </BrowserRouter>
            </div>
        );
    }
}

export default Layout;
