
import { TextField } from "@mui/material";
import { useState } from "react";
import SingleCoupon from "../../modelsUI/SingleCoupon/SingleCoupon";
import store from "../../redux/Store";




function CouponsByCompany(company:any): JSX.Element {
    const coupons = (store.getState().couponState.coupons);
    const [search,setSearch] = useState("");
    const setValue = (args:React.SyntheticEvent)=>{
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
    };
    return (

        <div className="CouponsByCompany">
            <h2>{company.company.name} Coupons:</h2><hr/> 
            {coupons.filter(item=> item.companyID === company.company.id).length>0&& <TextField 
            type="text"  label={"search in " +company.company.name+" coupons"} placeholder={"search coupon"} onChange={setValue} value={search}
            variant="outlined"/>  }      
            {coupons.filter(item=> item.companyID === company.company.id).length==0 && 
            <>This company doesn't have coupons.<br/>It is recommended to contact the company<br/>
            Email: {company.company.email}</>
            }
            <div className="Container PoppingContainer">
             {coupons.filter(coupon=>coupon.title.toLowerCase().includes(search.toLowerCase())||
            coupon.description.toLowerCase().includes(search.toLowerCase()))
            .filter(item=> item.companyID === company.company.id)
             .map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>)}
            </div>
               
            
        </div>
    );
}

export default CouponsByCompany;