import { Button, Dialog } from "@mui/material";
import { useEffect, useState } from "react";
import SingleCoupon from "../../modelsUI/SingleCoupon/SingleCoupon";
import Purchase from "../../customerAction/Purchase";
import { Coupon } from "../../models/Coupon";
import { forceLogoutAction } from "../../redux/AuthRedux";

import store from "../../redux/Store";
import "./MyCart.css";
import { useNavigate } from "react-router";



function MyCart(): JSX.Element {
    const nav = useNavigate();
    useEffect(()=>{
        if (store.getState().authState.role==="company" || store.getState().authState.role==="admin"){
            nav("/");
            return;
        }
    },[])

    const [coupons,setCoupons] = useState(store.getState().cartState.cartCoupons);
    store.subscribe(()=>setCoupons(store.getState().cartState.cartCoupons));
    const [cartCounter,setCartCounter] = useState(store.getState().cartState.cartCoupons.length);
    store.subscribe(()=>setCartCounter(store.getState().cartState.cartCoupons.length));

    const [totalPrice,setTotalPrice] = useState(0);
    
    const updateTotalPrice = () => {
        var counter = 0;
        coupons.map(item=> counter+=item.price);
        setTotalPrice(counter);
    }

    useEffect(updateTotalPrice);
    const [purchaseCoupons, setPurchaseCoupons]=useState<Coupon[]>([]);

   const [openRemov, setOpenRemov]= useState(false);
   const [open, setOpen] = useState(false);
   store.subscribe(()=>!(store.getState().authState.id>0)&&setOpen(store.getState().authState.id>0));
    const handelPurchas = ()=>{
        //If not logged in, open the Login panel
        if (!store.getState().authState.isLogged){
            store.dispatch(forceLogoutAction());
        //If there are coupons already previously purchased on the server, pop up a message
        } else if (coupons.filter(item=>item.isBought).length>0){
                    setOpenRemov(true);
                    }else{
                    //Contact us to purchase coupons, and receive back the list of coupons that were actually purchased 
                    
                    setPurchaseCoupons(Purchase());                 
                    
                    //Opens a message with the list of coupons purchased and / or failed
                    setOpen(true);
                    }
            
    }

    
    
    const handleClose= ()=>{
        setOpen(false);
    }

    const handleCloseRemov= ()=>{
        setOpenRemov(false);
    }

    const allSucccess = () :JSX.Element =>{
        return(
        <div className="PurchasedCouponPopping">
            <h2>Successfully Purchased:</h2><hr/>
            <div className="Container PoppingContainer">{purchaseCoupons.map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>)}</div>
        </div>
        );
    }

    const allFilad = () :JSX.Element=>{
        return(
            <div className="PurchasedCouponPopping">
                <h2>Failed to Purchase:</h2><hr/>
                <div className="Container PoppingContainer">
                    {store.getState().cartState.cartCoupons.map(item=>
                    <span key={item.id}><SingleCoupon couponData={item}/><div>{item.couponErr}</div></span>
                    )}
                </div>
            </div>
        );
    }
    
    
    return (
        <div className="MyCart">
            <div className="TheCart">
                <div className="CartTitle"><span className="Head">My Cart</span><span className="ItemCounter">{cartCounter} Item{cartCounter>1 && "s"}</span><span className="TotalPrice">{totalPrice.toFixed(2)}</span></div>
                <div className="Container">
			        {coupons.map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>)}
                </div>
                {coupons.length>0 && <button onClick={handelPurchas}>Purchase</button>}
                <Dialog disableEnforceFocus={true} open={open} onClose={handleClose}>
                    {store.getState().cartState.cartCoupons.length>0 && allFilad()}
                    {purchaseCoupons.length>0 && allSucccess()}
                    <Button variant="contained"  onClick={()=>{setOpen(false)}} style={{width: 140, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{"close"}</Button>
                </Dialog>

                <Dialog disableEnforceFocus={true} open={openRemov} onClose={handleCloseRemov}>
                    <div className="AlreadyBoughtMsg PoppingForm">
                    There are coupons in the cart that you have already purchased<br/>
                    Please remove them before purchasing<br/><br/>
                    <button className="PoppingFormButton WhiteButton" onClick={handleCloseRemov}>ok</button>
                    </div>
                </Dialog>
            </div>
        </div>
    );
}

export default MyCart;
