import { countReset } from "console";
import globals from "../../Utils/Globals";
import UpdateStoreCoupon from "../coupons/UpdateStoreCoupon/UpdateStoreCoupon";
import { Coupon } from "../models/Coupon";
import { UserCred } from "../models/UserCred";
import { UserData } from "../models/UserData";


//app state
export class AuthState{
    public coupons:Coupon[] =[];   
    public name: string ="guest";
    public lastName: string ="";
    public role: string="guest";
    public email:string="";
    public jwt:string="";
    public id: number=0;
    public isLogged:boolean=false;
    public isOpen:boolean=false;
}

//action type
export enum AuthActionType{
    UserLogin = "UserLogin",
    UserLogout = "UserLogout",
    JwtUpdate = "JwtUpdate",  //jwt, info changed
    UpdateUserCouponList = "UpdateUserCouponList",
    AddUserCouponList = "AddUserCouponList",
    UpdateUserDetails = "UpdateUserDetails",
    ForceLogout = "ForceLogout",
    SetOpen= "SetOpen",
    UpdateFromLocalStorage = "UpdateFromLocalStorge"
    
}


//Action 
export interface AuthAction{
    type:AuthActionType;
    payload?:any;
}


//functions for diffrent actions
export function loginAction(cred:UserCred):AuthAction{
    return {type: AuthActionType.UserLogin , payload:cred};
}

export function setOpenAction(open:boolean):AuthAction{
    return {type: AuthActionType.SetOpen , payload:open};
}

export function logoutAction():AuthAction{
    return {type: AuthActionType.UserLogout, payload: null}
}

export function userUpdateAction(jwt:string):AuthAction{
    return {type: AuthActionType.JwtUpdate, payload: (jwt)}
}

export function updateUserCouponsAction(coupons:Coupon[]):AuthAction{
    return {type: AuthActionType.UpdateUserCouponList, payload: (coupons)}
}

export function addUserCouponsAction(coupon:Coupon):AuthAction{
    return {type: AuthActionType.AddUserCouponList, payload: (coupon)}
}

export function updateUserDetailsAction(userDate:UserData):AuthAction{
    return {type: AuthActionType.UpdateUserDetails, payload: (userDate)}
}

export function forceLogoutAction():AuthAction{
    return {type: AuthActionType.ForceLogout, payload: null}
}

export function UpdateFromLocalStorageAction(authState:AuthState):AuthAction{
    return {type: AuthActionType.UpdateFromLocalStorage, payload: authState}
}




//reducer
export function AuthReducer(currentState: AuthState = new AuthState, action: AuthAction):AuthState{
    let newState = {...currentState};

    switch (action.type){
        case AuthActionType.UserLogin:
            if (action.payload.role === "customer" )action.payload.coupons.map((item: { isBought: boolean; })=>item.isBought=true)
            newState.coupons=action.payload.coupons;
            newState.email=action.payload.email;
            newState.id=action.payload.id;
            if (action.payload.name==undefined) {
                newState.name=action.payload.firstName;
                newState.lastName=action.payload.lastName;
                } else {newState.name=action.payload.name;
                 }
            if (action.payload.role == "admin") {
                newState.name ="The Admin"
            } 
            newState.role=action.payload.role;
            localStorage.setItem("AuthState",JSON.stringify(newState));
            break;

        case AuthActionType.UserLogout:
            newState.isLogged=false;
            newState.jwt="";
            newState.coupons =[];     
            newState.name ="guest";
            newState.lastName ="";
            newState.role ="guest";
            newState.email ="";
            newState.id =0;
            localStorage.removeItem("AuthState");
            break;

        case AuthActionType.JwtUpdate:
            newState.jwt=action.payload;
            newState.isLogged=true;
            newState.isOpen=false;
            localStorage.setItem("AuthState",JSON.stringify(newState));
            break;

        case AuthActionType.UpdateUserCouponList:
            if (action.payload.role === "customer" )action.payload.coupons.map((item: { isBought: boolean; })=>item.isBought=true)    
            newState.coupons=action.payload;
            localStorage.setItem("AuthState",JSON.stringify(newState));
            break;
        
        case AuthActionType.UpdateUserDetails:
            newState.name=action.payload.name;
            newState.lastName=action.payload.lastName;
            newState.email=action.payload.email;
            localStorage.setItem("AuthState",JSON.stringify(newState));
            break;

        case AuthActionType.ForceLogout:
            newState.isOpen=true;
            newState.isLogged=false;
            currentState.id!==0 ? newState.id =-1: newState.id=-2;
            if (window.location.href===globals.general.hrefRegister){newState.id=-3;};
            localStorage.setItem("AuthState",JSON.stringify(newState));
            break;

        case AuthActionType.SetOpen:
            newState.isOpen=action.payload;
            break;
        
        case AuthActionType.AddUserCouponList:
            newState.coupons.push(action.payload);
            localStorage.setItem("AuthState",JSON.stringify(newState));
            break;
    
        case AuthActionType.UpdateFromLocalStorage:
            newState=action.payload;
            break;
    
            }
    return newState;
}
