import { Button, Dialog, TextField } from "@mui/material";
import React, { useState } from "react";
import store from "../../redux/Store";
import Login from "../../poppings/LoginForm/LoginForm";
import "./MyAccount.css";
import ProfileForm from "../../poppings/ProfileForm/ProfileForm";
import ChangePassword from "../../poppings/ChangePassword/ChangePassword";
import SingleCoupon from "../../modelsUI/SingleCoupon/SingleCoupon";

function MyAccount(): JSX.Element {
    const role = store.getState().authState.role;
    const name = store.getState().authState.name;
    const lastName = store.getState().authState.lastName;
    const email = store.getState().authState.email;
    const coupons = (role==="admin"?[]:store.getState().authState.coupons);
    const companies = store.getState().adminState.companies;
    const customers = store.getState().adminState.coustomers;
    const [loginOpen,setLoginOpen] = React.useState(false);
    const [profileOpen,setProfileOpen] = React.useState(false);
    const [passwordOpen,setPasswordOpen] = React.useState(false);

    const handleLoginToOpen = () => {
        setLoginOpen(true);
    };
      
      const handleLoginToClose = () => {
        setLoginOpen(false);
    };

    const handleProfileToOpen = () => {
        setProfileOpen(true);
    };
      
      const handleProfileToClose = () => {
        setProfileOpen(false);
    };

    const handlePasswordToOpen = () => {
        setPasswordOpen(true);
    };
      
      const handlePasswordToClose = () => {
        setPasswordOpen(false);
    };

    const guestDetails = ():JSX.Element => {
        return (
            <div className="MyAccount">
                There are no details<br/>
                <button onClick={handleLoginToOpen}>Login</button>
                <Dialog open={loginOpen} onClose={handleLoginToClose}><Login/></Dialog>
            </div>
        )
    }
    
    const [search,setSearch] = useState("");
    const setValue = (args:React.SyntheticEvent)=>{
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
    };

    const customerDetails = ():JSX.Element => {
        return (
            <div className="MyAccount">
                First Name: {name} <br/>
                Last Name: {lastName} <br/>
                Email: {email} <br/>
                Coupons: {coupons.length}<br/>
                <button onClick={handleProfileToOpen}>Change Details</button>
                <button onClick={handlePasswordToOpen}>Change Password</button>
                {coupons.length>0 && <TextField 
                type="text"  label="search in your coupon" placeholder={"search coupon"} onChange={setValue} value={search}
                variant="outlined"/>}
                <div className="Container">{coupons.filter(coupon=>coupon.title.toLowerCase().includes(search.toLowerCase())||
                coupon.description.toLowerCase().includes(search.toLowerCase()))
                .map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>)}</div>
                <Dialog disableEnforceFocus={true} open={profileOpen} onClose={handleProfileToClose}><ProfileForm/></Dialog>
                <Dialog disableEnforceFocus={true} open={passwordOpen} onClose={handlePasswordToClose}><ChangePassword/>
                <Button variant="contained"  onClick={()=>{setPasswordOpen(false)}} style={{width: 140, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{"close"}</Button></Dialog>
            </div>
        )
    }

    const companyDetails = ():JSX.Element => {
        return (
            <div className="MyAccount">
                Company Name: {name} <br/>
                Company Email: {email} <br/>
                Coupons: {coupons.length}<br/>
                <button onClick={handleProfileToOpen}>Change Details</button>
                <button onClick={handlePasswordToOpen}>Change Password</button>
                <Dialog disableEnforceFocus={true} open={profileOpen} onClose={handleProfileToClose}><ProfileForm/></Dialog>
                <Dialog disableEnforceFocus={true} open={passwordOpen} onClose={handlePasswordToClose}><ChangePassword/>
                <Button variant="contained"  onClick={()=>{setPasswordOpen(false)}} style={{width: 140, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{"close"}</Button></Dialog>
            </div>
        )
    }

    const adminDetails = ():JSX.Element => {
        return (
            <div className="MyAccount">
                Name: Tal? Matan? Shiri? Roei? Is that you??? <br/>
                Email: {email} <br/>
                companies:{companies.length} <br/>
                customers: {customers.length}
            </div>
        )
    }

    return (
        <div>
            {role==="guest" && guestDetails()}
            {role==="customer" && customerDetails()}
			{role==="company" && companyDetails()}
            {role==="admin" && adminDetails()}
        </div>
    );
}

export default MyAccount;
