import { Button, Dialog, Link } from "@mui/material";
import { useState } from "react";
import CouponsByCoustomer from "../../mainpages/CouponsByCoustomer/CouponsByCoustomer";
import { UserCred } from "../../models/UserCred";
import ConfirmDelete from "../../poppings/ConfirmDelete/ConfirmDelete";
import ProfileForm from "../../poppings/ProfileForm/ProfileForm";
import store from "../../redux/Store";
import "./SingleCustomer.css";

interface singleCustomerProps{
    customerData:UserCred;
}

function SingleCustomer(props:singleCustomerProps): JSX.Element {
    const [updateCustomerForm, setUpdateCustomerForm]  = useState(false);
    const [deleteConfirmation,setDeleteConfirmation]   = useState(false);
    const [customerCouponsForm,setCustomerCouponsForm] = useState(false);

    const deleteCustomer = ()=>{
        setDeleteConfirmation(true);
    }
   
    const update = ()=>{
        setUpdateCustomerForm(true);
    }    

    const handleCloseUpdateForm = ()=>{
        setUpdateCustomerForm(false);
    }

    const handleOpenCustomerCoupons = ()=>{
        setCustomerCouponsForm(true);
    }
    
    const handleCloseCustomerCoupons = ()=>{
        setCustomerCouponsForm(false);
    }

    return (
        <div className="SingleCustomer" id="customer">
            <div className="CustomerName" onClick={handleOpenCustomerCoupons}>{props.customerData.firstName+" "+props.customerData.lastName}</div>
            <div className="CustomerEmail">{props.customerData.email}</div>
            <div className="CustomerCouponsCount">Coupons: {props.customerData.coupons.length}</div>
            {store.getState().authState.role==="admin" &&
                <> <div className="DeleteButton"><button onClick={deleteCustomer}>delete</button></div> 
                <div className="UpdateButton"><button onClick={update}>update</button></div></>
            }
            
            <Dialog disableEnforceFocus={true} open={updateCustomerForm} onClose={()=>{setUpdateCustomerForm(false)}}>
                <ProfileForm userCred={props.customerData}/>
                <Button variant="contained"  onClick={handleCloseUpdateForm} style={{width: 140, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{"סיום"}</Button>
            </Dialog>
            <Dialog disableEnforceFocus={true} open={deleteConfirmation}><ConfirmDelete func={setDeleteConfirmation} delet={props.customerData}/></Dialog>
            <Dialog disableEnforceFocus={true} open={customerCouponsForm} onClose={handleCloseCustomerCoupons}>
                <CouponsByCoustomer coustomer={props.customerData}/>
                <Button variant="contained"  onClick={handleCloseCustomerCoupons} style={{width: 140, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{"אישור"}</Button>
            </Dialog>
        </div>
    );
}

export default SingleCustomer;
