
import { TextField } from "@mui/material";
import { useState } from "react";
import SingleCoupon from "../../modelsUI/SingleCoupon/SingleCoupon";
import { Coupon } from "../../models/Coupon";




function CouponsByCoustomer(coustomer:any): JSX.Element {
    const [search,setSearch] = useState("");
    const setValue = (args:React.SyntheticEvent)=>{
    const value = (args.target as HTMLInputElement).value;
    setSearch(value);
    };
    
    
    const coustomerCoupons:Coupon[]=coustomer.coustomer.coupons;
    console.log(coustomer);
    return (
        <div className="CouponsByCustomer">
            <h2>{coustomer.coustomer.firstName+" "+coustomer.coustomer.lastName} Coupons:</h2><hr/>
            <TextField 
            type="text"  label={"search in " +coustomer.coustomer.firstName+" coupons"} placeholder={"search coupon"} onChange={setValue} value={search}
            variant="outlined"/>
            {coustomerCoupons.length==0 && 
            "This coustomer does not yet have coupons, It is recommended to contact the coustomer. email: "+coustomer.coustomer.email}
           <div className="Container PoppingContainer">{coustomerCoupons.filter(coupon=>coupon.title.toLowerCase().includes(search.toLowerCase())||
            coupon.description.toLowerCase().includes(search.toLowerCase()))
           .map(item=><span key={item.id}><SingleCoupon couponData={item}/></span>)}</div>
            
        </div>
    );
}

export default CouponsByCoustomer;