
import "../PoppingForm.css";
import SingleCoupon from "../../modelsUI/SingleCoupon/SingleCoupon";
import { useEffect, useState } from "react";
import store from "../../redux/Store";
import { Button } from "@mui/material";

interface myProps{
    title:string;
    func:Function;
}

function SuccessfullyAdded(props:myProps): JSX.Element {
    const handelClose =()=>{
        props.func(false);
    };
    const [coupons,setCoupons]=useState(store.getState().couponState.coupons);
    

    useEffect(()=>{
        const unSub = store.subscribe(()=>setCoupons(store.getState().couponState.coupons));
        return unSub;
    },[]);
    
    return (
            <div className="SuccessfullyAdded PoppingForm">
                <div className="Congrats">Congragulations! You king/queen!</div>
                {coupons.filter(item=> item.title === props.title)
                .map((item)=>{return <span key={item.id}><SingleCoupon couponData={item}/></span>})}
                <br/>
                <Button className='PoppingFormButton' onClick={handelClose}>Close</Button>
            </div>
    );
    
}

export default SuccessfullyAdded;
