
import { couponsUpdateAction } from "../redux/CouponRedux";
import { cartUpdateAction } from "../redux/CartRedux";
import store from "../redux/Store";

function SetBought() {

  

    const userCoupons=store.getState().authState.coupons;
    const siteCoupons=store.getState().couponState.coupons;
    const cartCoupons=store.getState().cartState.cartCoupons;
    
    userCoupons.map(userCuopon=>
        siteCoupons.filter(coupon=>coupon.id==userCuopon.id).map(item=>item.isBought=true));

    store.dispatch(couponsUpdateAction(siteCoupons));

    userCoupons.map(userCuopon=>
        cartCoupons.filter(coupon=>coupon.id==userCuopon.id).map(item=>{item.isBought=true;
                                                                        item.couponErr="must remove";}));

    store.dispatch(cartUpdateAction(cartCoupons));

}
export default SetBought;