import { TextField } from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router";
import SingleCompany from "../../modelsUI/SingleCompany/SingleCompany";
import store from "../../redux/Store";
import "./AllCompanies.css";

function AllCompanies(): JSX.Element {
    const nav = useNavigate();
    if (store.getState().authState.role!="admin"){nav("/");}
    const [search,setSearch] = useState("");
    const setValue = (args:React.SyntheticEvent)=>{
        const value = (args.target as HTMLInputElement).value;
        setSearch(value);
    };

    const companies=store.getState().adminState.companies;
    console.log("מהקומפניס "+companies)
    return (
    <div>
        <br/>
        <div >
            <TextField 
            type="text" label="search company" placeholder={"search company"} onChange={setValue} value={search}
            variant="outlined"/>
        </div>
        <br/>
        <div className="AllCompanies"> 
        <div className="Container" >
            {companies.filter(comp=>comp.name.toLowerCase().includes(search.toLowerCase())||
            comp.email.toLowerCase().includes(search.toLowerCase()))
            .map(company=> <span key={company.id}><SingleCompany companyData={company}/></span>)}
        </div>
    </div>
    </div>
    );
}

export default AllCompanies;
function nav(arg0: string) {
    throw new Error("Function not implemented.");
}

