import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import AddCouponIcon from '@mui/icons-material/AddCard';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {useForm,SubmitHandler} from "react-hook-form";
import { Autocomplete, } from '@mui/material';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import React, { useState } from 'react';
import "../PoppingForm.css";
import { Coupon } from '../../models/Coupon';
import UpdateUserCoupons from '../../coupons/UpdateUserCoupons/UpdateUserCoupons';
import UpdateStoreCoupon from '../../coupons/UpdateStoreCoupon/UpdateStoreCoupon';
import { grey } from '@mui/material/colors';
import jwtAxios from '../../../Utils/Interceptor';
import { Method } from 'axios';
import globals from '../../../Utils/Globals';

interface formable{
  title:string;
  description:string;
  price:number;
  category:string;
  amount:number;
  startDate:string;
  endDate:string;
  image?:string;
  id?:number;
}

interface myProps{
  coupon?:Coupon;
  func:Function;
}


function CouponForm(props:myProps): JSX.Element{

  const {register,handleSubmit,formState:{errors}} = useForm<formable>();
  const theme = createTheme();
  const [startDate, setStartDate] = React.useState<Date | null>(null);
  const [endDate, setEndDate] = React.useState<Date | null>(null);
  const categories = ['FASHION','ELECTRICITY','VACATION',"GROCERY","RESTAURANT"];
  const [serverError, setServerError]=React.useState<String>("");
  
    

  const successful =  (couponTitel:string) => {
    props.func(couponTitel);
     UpdateUserCoupons();
  }

  const failed =  (couponError:string) => {
    setServerError(couponError);
    console.log(couponError);
    console.log('was here');
  }

  
  const onSubmit:SubmitHandler<formable> = (data) => {  
    data.endDate+="T12:55:42.178+0300";
    data.startDate+="T12:55:42.178+0300";
    let axiosType:Method="post";
    let url=globals.company.addCoupon;
    if (props.coupon) {
      data.id=props.coupon.id;
      url=globals.company.updateCoupon;
      axiosType="put";
    };
    jwtAxios({method:axiosType, url:url, data:data}).then(res=>{
      successful(data.title);
    })
    .catch(error=>{
      console.log(error);
      failed(error.response.data.description);        
    });
  }

  if (props.coupon && endDate===null) {
    var start = new Date();
    start.setFullYear((Number)(props.coupon.startDate.substring(0,4)));
    start.setMonth((Number)(props.coupon.startDate.substring(5,7)));
    start.setDate((Number)(props.coupon.startDate.substring(8,10)));
    setStartDate(start);
    var end = new Date();
    end.setFullYear((Number)(props.coupon.endDate.substring(0,4)));
    end.setMonth((Number)(props.coupon.endDate.substring(5,7)));
    end.setDate((Number)(props.coupon.endDate.substring(8,10)));
    setEndDate(end);
  }

  return (
    <ThemeProvider theme={theme}>
          <Box className='CouponForm PoppingForm'>
          <Avatar>
            <AddCouponIcon sx={{fontSize: 30, color: grey}}/>
          </Avatar>
          <Typography sx={{fontSize: 30}}>{props.coupon?"Updating Coupon":"Adding Coupon"}</Typography>
            <Box component="form"  onSubmit={handleSubmit(onSubmit)}>
              <TextField
                className='PoppingFormText'
                required
                style={{width: 140, height:50,margin:10}}
                id="title"
                label="Title"
                defaultValue={props.coupon?props.coupon.title:""}
                variant="outlined" {...register("title",{maxLength:20, required:true })}

              />
              <TextField
                className='PoppingFormText'
                margin="normal"
                style={{width:140,height:50,margin:10}}
                label="Image"
                id="image"
                defaultValue={props.coupon?props.coupon.image:""}
                variant="outlined" {...register("image",{required:false})}
              />
              <br/>
              <TextField
                className='PoppingFormText'
                margin="normal"
                required
                style={{width: 300, height:50,margin:10}}
                label="Description"
                type="description"
                id="description"
                defaultValue={props.coupon?props.coupon.description:""}
                autoComplete="description"
                variant="outlined" {...register("description",{maxLength:100,required:true})}
              />
              <br/>
              <TextField
                className='PoppingFormText'
                margin="normal"
                required
                style={{width: 140, height:50,margin:10}}
                label="Price"
                type="number"
                id="price"
                defaultValue={props.coupon?props.coupon.price:""}
                autoComplete="price"
                variant="outlined" {...register("price",{min:1,required:true})}
              />
              <TextField
                className='PoppingFormText'
                margin="normal"
                required
                style={{width: 140, height:50,margin:10}}
                label="Amount"
                type="number"
                id="amount"
                autoComplete="amount"
                defaultValue={props.coupon?props.coupon.amount:""}
                variant="outlined" {...register("amount",{min:1,required:true})}
              />
              <br/>
              <Autocomplete
                className='PoppingFormText'
                disableClearable
                style={{width: 300, height:50,margin:10}}
                options={categories}
                defaultValue={props.coupon?categories.filter(item=>item===props.coupon?.category).at(0):""}
                id="category"
                renderInput={(params) => <TextField {...params} required label="Category" {...register("category",{required:true})}/>}
              />
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DatePicker
                label="Start Date"
                value={startDate}
                inputFormat="yyyy-MM-dd"
                mask=""
                onChange={(newStart) => {
                  setStartDate(newStart);
                }}
                renderInput={(params) => <TextField {...params} className='PoppingFormText' style={{width: 140, height:50,margin:10}} {...register("startDate",{required:true})} />}
               />
              </LocalizationProvider>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DatePicker
                label="End Date"
                value={endDate}
                inputFormat="yyyy-MM-dd"
                mask=''
                onChange={(newEnd) => {
                  setEndDate(newEnd);
                }}
                renderInput={(params) => <TextField {...params} className='PoppingFormText' style={{width: 140, height:50,margin:10}} {...register("endDate",{required:true})}/>}
               />
              </LocalizationProvider>
              <br/>
              <Button variant="contained" type="submit" style={{width: 300, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>{props.coupon?"Update Coupon":"Add Coupon"}</Button>
              <div style={{color:"red"}}>
                {(errors.title && "title max length 20") || (errors.description && "description max length 100") ||
                (errors.price && "price min 1") || (errors.amount && "amount min 1")}
                {serverError}
              </div>
            </Box>
          </Box>
    </ThemeProvider>
  );
}

export default CouponForm;