import { Button, Dialog, Link } from "@mui/material";
import { useState } from "react";
import CouponsByCompany from "../../mainpages/CouponsByCompany/CouponsByCompany";
import Company from "../../models/Company";
import CompanyForm from "../../poppings/CompanyForm/CompanyForm";
import ConfirmDelete from "../../poppings/ConfirmDelete/ConfirmDelete";
import store from "../../redux/Store";
import "../../poppings/PoppingForm.css";
import "./SingleCompany.css";

interface singleCompanyProps{
    companyData:Company
}


function SingleCompany(props:singleCompanyProps): JSX.Element {
    
    
    const [updateCompanyForm, setUpdateCompanyForm] = useState(false);
    const [updatedMessage, setUpdatedMessage] = useState(false);
    const [deleteConfirmation,setDeleteConfirmation] = useState(false);
    const [companyCouponsForm,setCompanyCouponsForm]=useState(false);

    const deleteCompany = ()=>{
        setDeleteConfirmation(true);
    }
   
    const update = ()=>{
        setUpdateCompanyForm(true);
    }

    const handelCloseMsg=()=>{
        setUpdatedMessage(false);
    }

    const handelCloseUpdatForm = ()=>{
        setUpdateCompanyForm(false);
        setUpdatedMessage(true);
    }

    const handelOpenCompanyCoupon = ()=>{
        setCompanyCouponsForm(true);
    }
    
    const handelCloseCompanyCoupon = ()=>{
        setCompanyCouponsForm(false);
    }
    
    return (
        <div className="SingleCompany" id="company">
            <div className="Name" onClick={handelOpenCompanyCoupon}>{props.companyData.name}</div>
            <div className="Email" >{props.companyData.email}</div>
            <div className="CouponsNumber" >Coupons: {props.companyData.coupons.length}</div>
            {store.getState().authState.role==="admin"&&
           <> <div className="DeleteButton"><button onClick={deleteCompany}>delete</button></div> 
            <div className="UpdateButton"><button onClick={update}>update</button></div></>
            }
            
            <Dialog disableEnforceFocus={true} open={updateCompanyForm} onClose={()=>{setUpdateCompanyForm(false)}}><CompanyForm func={handelCloseUpdatForm} company={props.companyData}/></Dialog>
            <Dialog disableEnforceFocus={true} open={updatedMessage} onClose={()=>{setUpdatedMessage(false)}}>
                <div className="CompanyUpdated PoppingForm">Company Details Changed
                <br/><br/><button className="PoppingFormButton WhiteButton" onClick={handelCloseMsg}>Close</button>
                </div>
            </Dialog>
            <Dialog disableEnforceFocus={true} open={deleteConfirmation}><ConfirmDelete func={setDeleteConfirmation} delet={props.companyData}/></Dialog>
            <Dialog disableEnforceFocus={true} open={companyCouponsForm} onClose={handelCloseCompanyCoupon}>
                <CouponsByCompany company={props.companyData}/>
                <Button variant="contained"  onClick={handelCloseCompanyCoupon} style={{width: 140, height:50,margin:10}} sx={{ mt: 3, mb: 2 }}>Close</Button>
            </Dialog>
            
        </div>
    );
}

export default SingleCompany;
