import Company from "../models/Company";
import { Coupon } from "../models/Coupon";
import { UserCred } from "../models/UserCred";
import { UserData } from "../models/UserData";
import store from "./Store";

//app state
export class AdminState{
    public companies:Company[] =[];
    public coustomers:UserCred[]=[];    
}

//action type
export enum AdminActionType{
    GetCompaniesList = "GetCompaniesList",
    setCoustomers= "setCoustomers",
    AdminLogout = "AdminLogout",
}


//Action 
export interface AdminAction{
    type:AdminActionType;
    payload?:any;
}


//functions for diffrent actions
export function getCompaniesAction(companies:Company[]):AdminAction{
    return {type: AdminActionType.GetCompaniesList , payload:companies};
}

export function setCastomersAction(coustomers:UserCred[]):AdminAction{
    return {type: AdminActionType.setCoustomers , payload:coustomers};
}

export function adminLogoutAction():AdminAction{
    return {type: AdminActionType.setCoustomers };
}

//reducer
export function AdminReducer(currentState: AdminState = new AdminState, action: AdminAction):AdminState{
    //get a copy of current state
    const newState = {...currentState};

    switch (action.type){
        case AdminActionType.GetCompaniesList:
            newState.companies=action.payload;
            break;
        case AdminActionType.setCoustomers:
            newState.coustomers=action.payload;
            break;
        case AdminActionType.AdminLogout:
            newState.companies=[];
            newState.coustomers=[];
    }

    return newState;
}
