import React from "react";
import Bag from "../../headers/Bag/Bag";
import Login from "../../headers/Login/Login";
import Logo from "../../headers/Logo/Logo";
import Menu from "../../headers/Menu/Menu";
import store from "../../redux/Store";
import "./HeaderLayout.css";

function HeaderLayout(): JSX.Element {

    return (
        <div className="HeaderLayout">
            <div  className="logo" >
                    <Logo/>
                </div>
                {(store.getState().authState.role==="guest" || store.getState().authState.role==="customer") &&
                <div className="bag">
                    <Bag/>
                </div>}
                <div className="login">
                    <Login/>
                </div>
                <div className="menu">
                    <Menu/>
                </div>
        </div>
    );
}

export default HeaderLayout;
