import globals from "../../../Utils/Globals";
import jwtAxios from "../../../Utils/Interceptor";
import { updateUserCouponsAction } from "../../redux/AuthRedux";
import { couponsUpdateAction } from "../../redux/CouponRedux";
import store from "../../redux/Store";
import "./UpdateUserCoupons.css";

async function UpdateUserCoupons() {
    jwtAxios.get(globals.company.allCoupons)
    .then((res) => {store.dispatch(updateUserCouponsAction(res.data))
                    store.dispatch(couponsUpdateAction(res.data))});
}

export default UpdateUserCoupons;